body {
  background-color: #dee4f1;
  min-height: 100vh;
  display: flex;
  flex-flow: row wrap;
  font-family: 'Roboto', sans-serif;
  font-size: 1.5rem;
  color: rgb(14, 12, 12);
  width: 100%;
}

h1, h2, h3, h4 {
  text-align: center;
}

.songtitle {
  margin-bottom: 0;
}

.container {
  width: 100%;
}

img {
  width: 100%;
  height: auto;
}

.flagg {
  max-height: 90px;
}

.imgDescription, .forsanger {
  font-size: 1rem;
  padding: 7px;
}

.forsanger {
  text-align: center;
  font-weight: 600;
}

p {
  max-width: 650px;
  padding: 10px 20px;
}

ul{
  /*list-style-type: none;*/
}